
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import DeviceRepairItem from './DeviceRepairItem'
import DeviceAddRepair from './DeviceAddRepair'
import api from '@/api'

export default {
name: 'DeviceRepairList',
components: {
Group,
Cell,
CellBox,
DeviceRepairItem,
DeviceAddRepair
},
props: {
device: Object
},
methods: {
saveStatus () {
console.log(this.device)
this.$store.commit('setCurrentRepair', this.device)
}
},
mounted () {
this.axios
.get(api.repair + this.device.id)
.then(response => {
this.repairs = response.data.data
})
.catch(error => {
console.log(error)
})
.finally()
},
data () {
return {
repairs: []
}
}
}



